<template lang="en">
    <section>
       <!-- <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('Working/NonWorkingDay')}}</span>
                    </div>
                </div>
            </div>
        </div>  -->
     <!-- <div class="border p-3">  -->
        <tg-list :IsShowAction="false" :showCheckBox="false" :listType="[DetailListType]" ModuleName="Timer" 
            SubModuleCode="WrokingDay" IdentityColumn="worK_DAY_ID" :HeaderText="$t('WorkingNonWorkingDay')" :ListData="list"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="ListWorkDay" :HeaderButtons="buttons"
            :TotalRecords="TotalRecords"  :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="ListWorkDay" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
             v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }"> 
                <template v-if="data.column.COLUMN_NAME == 'Edit'">
                    <a href="javascript:;" @click="OpenAddworkingDay(data.row.worK_DAY_ID)"><em class="fa fa-pencil text-success action-icon"></em></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Delete'">
                    <a href="javascript:;" @click="Delete(data.row.worK_DAY_ID)"><em class="fa fa-trash text-danger action-icon"></em></a>
                </template>
            </template>
        </tg-list>

<!-- </div> -->

</section>
</template>
<script>
import DataService from '../../services/DataService'
import moment from 'moment'
export default {

    data() {
        return {
            list: {},
            leftsearchSchema:[],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            Headers: [{
                "COLUMN_NAME": 'worK_DATE',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Date'),
                "DISPLAY_ORDER": 1,
            },
            {
                "COLUMN_NAME": 'iS_WORKING',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": 'locatioN_NAME',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Location'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Edit'),
                "DISPLAY_ORDER": 4,
                settings: {
                        isInSlot: true,
                    }
            },

            {
                "COLUMN_NAME": "Delete",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Delete'),
                "DISPLAY_ORDER": 5,
                settings: {
                        isInSlot: true,
                    }
            },


            ],
            buttons :
            [
                {
                    title: this.$t('AddWorkingDay'),
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddworkingDay',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    id:'AddWorkingDay',
                    checkPrivilege: false,
                    isEnabledOnSelectedRow: false
                    
                },
            ],
            widgets: [],
            LegendArray: [],
            DetailListType: 'List',
            CurrentPage: 1,
            TotalPages: 0,
            pageSize: 10,
            pageNumber: 1,
        }

    },
    created: async function () {
        //await this.Fetchdata();
        await this.ListWorkDay()

    },
    methods: {
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        Fetchdata: async function () {
            var vm = this;
            await DataService.WorkingDay().then(response => {
                vm.list = response.data;
                console.log("list", vm.list);

            })
        },
        ListWorkDay: async function () {
            var vm = this;
            var param=`sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.pageSize}&pageNum=${vm.pageNumber}`
            await DataService.ListWorkDay(param).then(response => {
                vm.list = response.data;
                console.log("list",vm.list);

                vm.list.forEach((row) => {
                    console.log("row",row)
                    row.iS_WORKING = row.iS_WORKING == 1 ? vm.$t('WorkingDay') : vm.$t('companyholidays')
                    row.worK_DATE=vm.formatOnlyDate(row.worK_DATE)
                });
        
                vm.TotalRecords=vm.list[0].totaL_REC;
                vm.TotalPages = Math.ceil(vm.TotalRecords / vm.pageSize);
                vm.CurrentPage = vm.pageNumber;

            })
        },
        formatOnlyDate: function (value) {
             if (value) {
                 return moment(value).format('MM/DD/YYYY');
             }
         },
        pagerMethod: function (value) {
            debugger
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.ListWorkDay();
        },
        commonHeaderButtonClick(event) {
            debugger
            switch (event.callbackfunction) {
                case "AddworkingDay":
                    this.OpenAddworkingDay(0);
                    break;

            }
        },
        OpenAddworkingDay:function(item){
            var vm = this;
            debugger;
            if (!vm.isBlank(item)) {
                vm.$router.push({ name: "AddWorkingDay", params: { id: item } });
            }
            else {
                vm.$router.push({ name: "AddWorkingDay", params: { id: 0 } });
            }
        },
        Delete:async function(id){
            var param=`id=${id}`
             await DataService.DeleteWoringDay(param).then((response) => {
                       console.log("response",response)
                       if(response.data=1){
                        this.ShowAlert(vm.$t('RecordsDeleteSuccessfully'), "success", true, vm.$t('Alert'));
                        this.ListWorkDay();
                       }
                    });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.ListWorkDay();
        },


    }
}
</script>